import { createRef } from 'preact/compat'
import BaseNode from './BaseNode'
import { GraphModel, HtmlNodeModel } from '../../model'

export type IHtmlNodeProps = {
  model: HtmlNodeModel
  graphModel: GraphModel
}

export class HtmlNode extends BaseNode<IHtmlNodeProps> {
  ref = createRef()
  currentProperties?: string
  preProperties?: string

  get rootEl() {
    return this.ref.current
  }

  /**
   * @overridable æ”¯æŒé‡å†™
   * è‡ªå®šä¹‰HTMLèŠ‚ç‚¹å†…å®¹
   * @param {HTMLElement} rootEl è‡ªå®šä¹‰HTMLèŠ‚ç‚¹å†…å®¹å¯ä»¥æŒ‚è½½çš„domèŠ‚ç‚¹
   * @example
   * class CustomHtmlNode extends HtmlNode {
   *   setHtml(rootEl) {
   *     const input = document.createElement('input');
   *     rootEl.appendChild(input)
   *   }
   * }
   */
  setHtml(rootEl: SVGForeignObjectElement) {
    rootEl.appendChild(document.createElement('div'))
  }

  // TODO: 1. åº”è¯¥åœ¨ä»€ä¹ˆæ—¶æœºè¿›è¡Œæ›´æ–°å‘¢ï¼Ÿ2. å¦‚ä½•ç²¾ç»†åŒ–æŽ§åˆ¶
  confirmUpdate(rootEl: SVGForeignObjectElement) {
    this.setHtml(rootEl)
  }

  /**
   * @overridable æ”¯æŒé‡å†™
   * å’Œreactçš„shouldComponentUpdateç±»ä¼¼ï¼Œéƒ½æ˜¯ä¸ºäº†é¿å…å‡ºå‘ä¸å¿…è¦çš„render.
   * ä½†æ˜¯è¿™é‡Œä¸ä¸€æ ·çš„åœ°æ–¹åœ¨äºŽï¼ŒsetHtmlæ–¹æ³•ï¼Œæˆ‘ä»¬åªåœ¨propertieså‘ç”Ÿå˜åŒ–äº†åŽå†è§¦å‘ã€‚
   * è€Œx,yç­‰è¿™äº›åæ ‡ç›¸å…³çš„æ–¹æ³•å‘ç”Ÿäº†å˜åŒ–ï¼Œä¸ä¼šå†é‡æ–°è§¦å‘setHtml.
   */
  shouldUpdate() {
    if (this.preProperties && this.preProperties === this.currentProperties) {
      return false
    }
    this.preProperties = this.currentProperties
    return true
  }

  componentDidMount() {
    console.log('HtmlNode --->>> componentDidMount - åˆå§‹åŒ–å†…å®¹')
    if (this.shouldUpdate() && this.rootEl) {
      this.setHtml(this.rootEl)
    }
  }

  componentDidUpdate() {
    console.log('HtmlNode --->>> componentDidUpdate - æ›´æ–°èŠ‚ç‚¹å†…å®¹')
    // DONE: å°† componentDidMount å’Œ componentDidUpdate åŒºåˆ†å¼€ï¼Œå¦‚æžœå†™åœ¨ä¸€æ¬¡ï¼Œæ¸²æŸ“ React ç»„ä»¶ä¼šé‡å¤åˆå§‹åŒ–ï¼Œæ¶ˆè€—è¿‡å¤šèµ„æº
    // ä¸ºäº†ä¿è¯åŽ†å²å…¼å®¹æ€§ï¼Œå…ˆå°†é»˜è®¤ HTML èŠ‚ç‚¹çš„ setHtml å’Œ confirmUpdate ä¿æŒä¸€ç›´ï¼Œç”¨æˆ·å¯é€šè¿‡è‡ªå®šä¹‰çš„æ–¹å¼é‡æ–°å®šä¹‰
    if (this.shouldUpdate() && this.rootEl) {
      this.confirmUpdate(this.rootEl)
    }
  }

  componentWillUnmount() {
    this.rootEl.innerHTML = ''
  }

  getShape() {
    const { model } = this.props
    const { x, y, height, width } = model
    const style = model.getNodeStyle()
    this.currentProperties = JSON.stringify(model.properties)
    return (
      <foreignObject
        {...style}
        x={x - width / 2}
        y={y - height / 2}
        width={width}
        height={height}
        ref={this.ref}
      />
    )
  }
}

export default HtmlNode
