import { createElement as h, Component } from 'preact/compat'
import { OutlineOverlay } from '.'
import { observer } from '../..'
import LogicFlow from '../../LogicFlow'
import { GraphModel } from '../../model'
import { Tool } from '../../tool'

type IProps = {
  graphModel: GraphModel
  tool: Tool
}

@observer
export class ToolOverlay extends Component<IProps> {
  // åœ¨reactä¸¥æ ¼æ¨¡å¼ä¸‹ï¼ŒuseEffectä¼šæ‰§è¡Œä¸¤æ¬¡ï¼Œä½†æ˜¯åœ¨LogicFlowå†…éƒ¨ï¼Œåˆ™åªä¼šè§¦å‘ä¸€æ¬¡componentDidMountå’ŒcomponentDidUpdateã€‚
  // å…¶ä¸­ç¬¬ä¸€æ¬¡componentDidMountå¯¹åº”çš„graphModelä¸ºè¢«ä¸¢å¼ƒçš„graphModel, æ‰€ä»¥ä¸åº”è¯¥ç”Ÿæ•ˆã€‚
  // åœ¨éžreactçŽ¯å¢ƒä¸‹ï¼Œåªä¼šè§¦å‘ä¸€æ¬¡componentDidMountï¼Œä¸ä¼šè§¦å‘componentDidUpdateã€‚
  // æ‰€ä»¥è¿™é‡Œé‡‡ç”¨componentDidUpdateå’ŒcomponentDidMountéƒ½åŽ»è§¦å‘æ’ä»¶çš„renderæ–¹æ³•ã€‚
  componentDidMount(): void {
    this.triggerToolRender()
  }

  componentDidUpdate(): void {
    this.triggerToolRender()
  }

  /**
   * å¤–éƒ¨ä¼ å…¥çš„ä¸€èˆ¬æ˜¯HTMLElement
   */
  getTools() {
    const { tool, graphModel } = this.props
    const { textEditElement } = graphModel
    const tools = tool.getTools()
    const components = tools.map((t) =>
      h(t, {
        textEditElement,
        graphModel,
        logicFlow: tool.instance,
      }),
    )
    tool.components = components
    return components
  }

  triggerToolRender() {
    const { tool, graphModel } = this.props
    const ToolOverlayElement = document.querySelector(
      `#ToolOverlay_${graphModel.flowId}`,
    ) as HTMLElement
    const lf: LogicFlow = tool.getInstance()
    lf.components.forEach((render) => render(lf, ToolOverlayElement))
    lf.components = [] // ä¿è¯extensionç»„ä»¶çš„renderåªæ‰§è¡Œä¸€æ¬¡
  }

  render() {
    const { graphModel } = this.props
    return (
      <div className="lf-tool-overlay" id={`ToolOverlay_${graphModel.flowId}`}>
        {this.getTools()}
      </div>
    )
  }
}

export default OutlineOverlay
