import { Component } from 'preact/compat'
import { cloneDeep, assign } from 'lodash-es'
import { observer } from '../..'
import { createUuid } from '../../util'
import { GraphModel } from '../../model'
import { DEFAULT_GRID_SIZE } from '../../constant'

import GridOptions = Grid.GridOptions

type IProps = GridOptions & {
  graphModel: GraphModel
}

@observer
export class Grid extends Component<IProps> {
  readonly id = createUuid()

  // ç½‘æ ¼ç±»åž‹ä¸ºç‚¹çŠ¶
  renderDot() {
    const { config, size = 1, visible } = this.props

    const { color, thickness = 2 } = config ?? {}

    // å¯¹äºŽç‚¹çŠ¶ç½‘æ ¼ï¼Œç‚¹çš„åŠå¾„ä¸èƒ½å¤§äºŽç½‘æ ¼å¤§å°çš„å››åˆ†ä¹‹ä¸€
    const radius = Math.min(Math.max(2, thickness), size / 4)
    const opacity = visible ? 1 : 0
    return (
      <g fill={color} opacity={opacity}>
        <circle cx={0} cy={0} r={radius / 2} />
        <circle cx={0} cy={size} r={radius / 2} />
        <circle cx={size} cy={0} r={radius / 2} />
        <circle cx={size} cy={size} r={radius / 2} />
      </g>
    )
  }

  // ç½‘æ ¼ç±»åž‹ä¸ºäº¤å‰çº¿
  // todo: é‡‡ç”¨èƒŒæ™¯ç¼©æ”¾çš„æ–¹å¼ï¼Œå®žçŽ°æ›´å¥½çš„ä½“éªŒ
  renderMesh() {
    const { config, size = 1, visible } = this.props
    const { color, thickness = 1 } = config ?? {}

    // å¯¹äºŽäº¤å‰çº¿ç½‘æ ¼ï¼Œçº¿çš„å®½åº¦ä¸èƒ½å¤§äºŽç½‘æ ¼å¤§å°çš„ä¸€åŠ
    const strokeWidth = Math.min(Math.max(1, thickness), size / 2)
    const d = `M 0 0 H ${size} V ${size} H 0 Z`
    const opacity = visible ? 1 : 0
    return (
      <path
        d={d}
        stroke={color}
        strokeWidth={strokeWidth}
        opacity={opacity}
        fill="transparent"
      />
    )
  }

  render() {
    const {
      type,
      size = 1,
      graphModel: { transformModel },
    } = this.props
    const { SCALE_X, SKEW_Y, SKEW_X, SCALE_Y, TRANSLATE_X, TRANSLATE_Y } =
      transformModel
    const matrixString = [
      SCALE_X,
      SKEW_Y,
      SKEW_X,
      SCALE_Y,
      TRANSLATE_X,
      TRANSLATE_Y,
    ].join(',')
    const transform = `matrix(${matrixString})`
    // const transitionStyle = {
    //   transition: 'all 0.1s ease',
    // };
    return (
      <div className="lf-grid">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          width="100%"
          height="100%"
        >
          <defs>
            <pattern
              id={this.id}
              patternUnits="userSpaceOnUse"
              patternTransform={transform}
              x="0"
              y="0"
              width={size}
              height={size}
            >
              {type === 'dot' && this.renderDot()}
              {type === 'mesh' && this.renderMesh()}
            </pattern>
          </defs>
          <rect width="100%" height="100%" fill={`url(#${this.id})`} />
        </svg>
      </div>
    )
  }
}

export namespace Grid {
  export type GridOptions = {
    /**
     * ç½‘æ ¼æ ¼å­é—´è·
     */
    size?: number
    /**
     * ç½‘æ ¼æ˜¯å¦å¯è§
     */
    visible?: boolean
    /**
     * ç½‘æ ¼ç±»åž‹
     * - `dot` ç‚¹çŠ¶ç½‘æ ¼
     * - `mesh` äº¤å‰çº¿ç½‘æ ¼
     */
    type?: 'dot' | 'mesh'
    config?: {
      /**
       * ç½‘æ ¼çš„é¢œè‰²
       */
      color: string
      /**
       * ç½‘æ ¼çš„å®½åº¦
       * - å¯¹äºŽ `dot` ç‚¹çŠ¶ç½‘æ ¼ï¼Œè¡¨ç¤ºç‚¹çš„å¤§å°
       * - å¯¹äºŽ `mesh` äº¤å‰çº¿ç½‘æ ¼ï¼Œè¡¨ç¤ºçº¿çš„å®½åº¦
       */
      thickness?: number
    }
  }

  export const defaultProps: GridOptions = {
    size: DEFAULT_GRID_SIZE,
    visible: true,
    type: 'dot',
    config: {
      color: '#ababab',
      thickness: 1,
    },
  }

  export function getGridOptions(options: number | boolean | GridOptions) {
    const defaultOptions = cloneDeep(Grid.defaultProps)
    if (typeof options === 'number') {
      return assign(defaultOptions, { size: options })
    } else if (typeof options === 'boolean') {
      return assign(defaultOptions, { visible: options })
    } else {
      return assign(defaultOptions, options)
    }
  }
}
