import LogicFlow from '../LogicFlow'
import Point = LogicFlow.Point

/**
 * æ±‚ä¸¤ä¸ªçº¿æ®µäº¤ç‚¹å…¥å‚ï¼šçº¿æ®µ1ç«¯ç‚¹: ab, çº¿æ®µ2ç«¯ç‚¹ï¼š cd,
 * @param a
 * @param b
 * @param c
 * @param d
 * @returnï¼šæœ‰äº¤ç‚¹è¿”å›žäº¤ç‚¹ Pointï¼Œæ— äº¤ç‚¹è¿”å›žfalse
 */
export const getCrossPointOfLine = (a: Point, b: Point, c: Point, d: Point) => {
  /** 1 è§£çº¿æ€§æ–¹ç¨‹ç»„, æ±‚çº¿æ®µäº¤ç‚¹. * */
  // å¦‚æžœåˆ†æ¯ä¸º0 åˆ™å¹³è¡Œæˆ–å…±çº¿, ä¸ç›¸äº¤
  const denominator = (b.y - a.y) * (d.x - c.x) - (a.x - b.x) * (c.y - d.y)
  if (denominator === 0) {
    return false
  }
  // çº¿æ®µæ‰€åœ¨ç›´çº¿çš„äº¤ç‚¹åæ ‡ (x , y)
  const x =
    ((b.x - a.x) * (d.x - c.x) * (c.y - a.y) +
      (b.y - a.y) * (d.x - c.x) * a.x -
      (d.y - c.y) * (b.x - a.x) * c.x) /
    denominator
  const y =
    -(
      (b.y - a.y) * (d.y - c.y) * (c.x - a.x) +
      (b.x - a.x) * (d.y - c.y) * a.y -
      (d.x - c.x) * (b.y - a.y) * c.y
    ) / denominator

  /** 2 åˆ¤æ–­äº¤ç‚¹æ˜¯å¦åœ¨ä¸¤æ¡çº¿æ®µä¸Š * */
  if (
    // äº¤ç‚¹åœ¨çº¿æ®µ1ä¸Š
    (x - a.x) * (x - b.x) <= 0 &&
    (y - a.y) * (y - b.y) <= 0 &&
    // ä¸”äº¤ç‚¹ä¹Ÿåœ¨çº¿æ®µ2ä¸Š
    (x - c.x) * (x - d.x) <= 0 &&
    (y - c.y) * (y - d.y) <= 0
  ) {
    // è¿”å›žäº¤ç‚¹p
    return {
      x,
      y,
    }
  }
  // å¦åˆ™ä¸ç›¸äº¤
  return false
}

/* åˆ¤æ–­ä¸€ä¸ªç‚¹æ˜¯å¦åœ¨çº¿æ®µä¸­
å…¥å‚ç‚¹ï¼špoint, çº¿æ®µèµ·ç»ˆç‚¹ï¼Œstart,end,
è¿”å›žå€¼ï¼š åœ¨çº¿æ®µä¸­trueï¼Œå¦åˆ™false
*/
export const isInSegment = (point: Point, start: Point, end: Point) => {
  const { x, y } = point
  const { x: startX, y: startY } = start
  const { x: endX, y: endY } = end
  const k = (endY - startY) / (endX - startX)
  const b = startY - k * startX
  return (
    x >= startX &&
    x <= endX &&
    y >= startY &&
    y <= endY &&
    Math.abs(y - k * x + b) < Number.EPSILON
  )
}
