import LogicFlow from '../LogicFlow'
import PointTuple = LogicFlow.PointTuple

export function snapToGrid(point: number, gridSize: number) {
  // ä¿è¯ x, y çš„å€¼ä¸º gridSize çš„æ•´æ•°å€
  return gridSize * Math.round(point / gridSize) || point
}

// èŽ·å–èŠ‚ç‚¹åç§»æ—¶ï¼Œäº§ç”Ÿçš„åç§»é‡ã€‚å½“èŠ‚ç‚¹åŸºäºŽgridSizeè¿›è¡Œäº†åç§»åŽï¼Œ
// èŠ‚ç‚¹ä¸Šçš„æ–‡æœ¬å¯ä»¥åŸºäºŽæ­¤æ–¹æ³•ç§»åŠ¨å¯¹åº”çš„è·ç¦»æ¥ä¿æŒä¸ŽèŠ‚ç‚¹ç›¸å¯¹ä½ç½®ä¸å˜ã€‚
export function getGridOffset(distance: number, gridSize: number) {
  return distance % gridSize
}

/**
 * å¤šè¾¹å½¢è®¾ç½® points åŽï¼Œåæ ‡å¹³ç§»è‡³åŽŸç‚¹ å¹¶ æ ¹æ® widthã€height ç¼©æ”¾
 * @param points
 * @param width
 * @param height
 */
export function normalizePolygon(
  points?: PointTuple[],
  width?: number,
  height?: number,
): PointTuple[] {
  if (!points) return []

  // è®¡ç®—è¾¹ç•Œæ¡†
  const minX = Math.min(...points.map((p) => p[0]))
  const maxX = Math.max(...points.map((p) => p[0]))
  const minY = Math.min(...points.map((p) => p[1]))
  const maxY = Math.max(...points.map((p) => p[1]))

  // å¹³ç§»è‡³åŽŸç‚¹
  const dx = -minX
  const dy = -minY
  const translatedPoints: PointTuple[] = points.map(([x, y]) => [
    x + dx,
    y + dy,
  ])

  // è®¡ç®—è¾¹ç•Œæ¡†çš„å®½åº¦å’Œé«˜åº¦
  const bboxWidth = maxX - minX
  const bboxHeight = maxY - minY

  // è®¡ç®—ç¼©æ”¾å› å­
  const scaleX = width ? width / bboxWidth : 1
  const scaleY = height ? height / bboxHeight : 1
  const scaleFactor = Math.min(scaleX, scaleY)

  // ç¼©æ”¾é¡¶ç‚¹
  return translatedPoints.map(([x, y]) => [x * scaleFactor, y * scaleFactor])
}
