  // åœ¨å›¾ä¸Šæ“ä½œåˆ›å»ºè¾¹æ—¶ï¼Œé»˜è®¤ä½¿ç”¨çš„è¾¹ç±»åž‹.
  @observable edgeType: string
  // å½“å‰å›¾ä¸Šæ‰€æœ‰èŠ‚ç‚¹çš„model
