  @observable edges: BaseEdgeModel[] = []
  // å¤–éƒ¨æ‹–åŠ¨èŠ‚ç‚¹è¿›å…¥ç”»å¸ƒçš„è¿‡ç¨‹ä¸­ï¼Œç”¨fakeNodeæ¥å’Œç”»å¸ƒä¸Šæ­£æ˜¯çš„èŠ‚ç‚¹åŒºåˆ†å¼€
  @observable fakeNode?: BaseNodeModel | null

  /**
   * å…ƒç´ é‡åˆæ—¶å †å æ¨¡å¼ï¼š
   * - DEFAULTï¼ˆé»˜è®¤æ¨¡å¼ï¼‰ï¼šèŠ‚ç‚¹å’Œè¾¹è¢«é€‰ä¸­ï¼Œä¼šè¢«æ˜¾ç¤ºåœ¨æœ€ä¸Šé¢ã€‚å½“å–æ¶ˆé€‰ä¸­åŽï¼Œå…ƒç´ ä¼šæ¢å¤ä¹‹å‰çš„å±‚çº§
   * - INCREASEï¼ˆé€’å¢žæ¨¡å¼ï¼‰ï¼šèŠ‚ç‚¹å’Œè¾¹è¢«é€‰ä¸­ï¼Œä¼šè¢«æ˜¾ç¤ºåœ¨æœ€ä¸Šé¢ã€‚å½“å–æ¶ˆé€‰ä¸­åŽï¼Œå…ƒç´ ä¼šä¿æŒå½“å‰å±‚çº§
   */
  @observable overlapMode = OverlapMode.DEFAULT
  // èƒŒæ™¯é…ç½®
  @observable background?: boolean | LFOptions.BackgroundConfig
  // ç½‘æ ¼å¤§å°
  @observable gridSize: number = 1
  // æŽ§åˆ¶ç”»å¸ƒçš„ç¼©æ”¾ã€å¹³ç§»
  @observable transformModel: TransformModel
  // æŽ§åˆ¶æµç¨‹å›¾ç¼–è¾‘ç›¸å…³é…ç½®é¡¹ Model
