/* èŽ·å–æ‰€æœ‰é”šç‚¹ */
export const getAnchors = (data: BaseNodeModel) => {
  const { anchors } = data
  return anchors
}

export type NodeContaint = {
  node: BaseNodeModel
  anchorIndex: number
  anchor: Model.AnchorConfig
}

/* æ‰‹åŠ¨è¾¹æ—¶èŽ·å–ç›®æ ‡èŠ‚ç‚¹çš„ä¿¡æ¯ï¼šç›®æ ‡èŠ‚ç‚¹ï¼Œç›®æ ‡èŠ‚ç‚¹çš„é”šç‚¹indexä»¥åŠåæ ‡ */
export const targetNodeInfo = (
  position: Point,
  graphModel: GraphModel,
): NodeContaint => {
  const { nodes } = graphModel
  let nodeInfo: NodeContaint
  for (let i = nodes.length - 1; i >= 0; i--) {
    const targetNode = nodes[i]
    const inNode = isInNodeBbox(position, targetNode)
    if (inNode) {
      const anchorInfo = targetNode.getTargetAnchor(position)
      if (anchorInfo) {
        // ä¸èƒ½è¿žæŽ¥åˆ°æ²¡æœ‰é”šç‚¹çš„èŠ‚ç‚¹
        const currentNodeInfo = {
          node: targetNode,
          anchorIndex: anchorInfo.index,
          anchor: anchorInfo.anchor,
        }
        // fix: 489 å¤šä¸ªèŠ‚ç‚¹é‡åˆæ—¶ï¼Œè¿žçº¿è¿žæŽ¥ä¸Šé¢çš„é‚£ä¸€ä¸ªã€‚
        if (!nodeInfo! || isNodeHigher(targetNode, nodeInfo.node, graphModel)) {
          nodeInfo = currentNodeInfo
        }
      }
    }
  }
  return nodeInfo!
}
/**
 * æ¯”è¾ƒä¸¤ä¸ªèŠ‚ç‚¹
 */
const isNodeHigher = (
  node1: BaseNodeModel,
  node2: BaseNodeModel,
  graphModel: GraphModel,
) => {
  if (node1.zIndex > node2.zIndex) {
    return true
  }
  return (
    graphModel.nodesMap[node1.id].index > graphModel.nodesMap[node2.id].index
  )
}

/* æ‰‹åŠ¨è¾¹æ—¶èŽ·å–ç›®æ ‡èŠ‚ç‚¹ä¸Šï¼Œè·ç¦»ç›®æ ‡ä½ç½®æœ€è¿‘çš„é”šç‚¹ */
export const getClosestAnchor = (
  position: Point,
  node: BaseNodeModel,
): AnchorInfo => {
  const anchors = getAnchors(node)
  let closest: AnchorInfo
  let minDistance = Number.MAX_SAFE_INTEGER
  for (let i = 0; i < anchors.length; i++) {
    const len = distance(position.x, position.y, anchors[i].x, anchors[i].y)
    if (len < minDistance) {
      minDistance = len
      closest = {
        index: i,
        anchor: {
          ...anchors[i],
          x: anchors[i].x,
          y: anchors[i].y,
          id: anchors[i].id,
        },
      }
    }
  }
  return closest!
}

/* ä¸¤ç‚¹ä¹‹é—´è·ç¦» */
export const distance = (
  x1: number,
  y1: number,
  x2: number,
  y2: number,
): number => Math.hypot(x1 - x2, y1 - y2)

/* æ˜¯å¦åœ¨æŸä¸ªèŠ‚ç‚¹å†…ï¼Œæ‰‹å¦è¿›è¡Œè¿žæŽ¥ï¼Œæœ‰offsetæŽ§åˆ¶ç²’åº¦ï¼Œä¸Žoutlineæœ‰å…³ï¼Œå¯ä»¥ä¼˜åŒ– */
export const isInNode = (position: Point, node: BaseNodeModel): boolean => {
  let inNode = false
  const offset = 0
  const bBox = getNodeBBox(node)
  if (
    position.x >= bBox.minX - offset &&
    position.x <= bBox.maxX + offset &&
    position.y >= bBox.minY - offset &&
    position.y <= bBox.maxY + offset
  ) {
    inNode = true
  }
  return inNode
}
export const isInNodeBbox = (position: Point, node: BaseNodeModel): boolean => {
  let inNode = false
  const offset = 5
  const bBox = getNodeBBox(node)
  if (
    position.x >= bBox.minX - offset &&
    position.x <= bBox.maxX + offset &&
    position.y >= bBox.minY - offset &&
    position.y <= bBox.maxY + offset
  ) {
    inNode = true
  }
  return inNode
}

export type NodeBBox = {
  x: number
  y: number
  width: number
  height: number
  minX: number
  minY: number
  maxX: number
  maxY: number
  centerX: number
  centerY: number
}

/* èŽ·å–èŠ‚ç‚¹bbox */
export const getNodeBBox = (node: BaseNodeModel): NodeBBox => {
  const { x, y, width, height } = node
  return {
    minX: x - width / 2,
    minY: y - height / 2,
    maxX: x + width / 2,
    maxY: y + height / 2,
    x,
    y,
    width,
    height,
    centerX: x,
    centerY: y,
  }
}
type RadiusCircle = {
  x: number
  y: number
  r: number
}
export const getRectRadiusCircle = (
  node: BaseNodeModel,
): [RadiusCircle, RadiusCircle, RadiusCircle, RadiusCircle] => {
  const { x, y, width, height, radius } = node as RectNodeModel
  return [
    {
      x: x - width / 2 + radius,
      y: y - height / 2 + radius,
      r: radius,
    },
    {
      x: x + width / 2 - radius,
      y: y - height / 2 + radius,
      r: radius,
    },
    {
      x: x - width / 2 + radius,
      y: y + height / 2 - radius,
      r: radius,
    },
    {
      x: x + width / 2 - radius,
      y: y + height / 2 - radius,
      r: radius,
    },
  ]
}

export const getClosestRadiusCenter = (
  point: Point,
  direction: Direction,
  node: BaseNodeModel,
): Point => {
  const radiusCenter = getRectRadiusCircle(node)
  let closestRadiusPoint: RadiusCircle
  let minDistance = Number.MAX_SAFE_INTEGER
  radiusCenter.forEach((item) => {
    const radiusDistance = distance(point.x, point.y, item.x, item.y)
    if (radiusDistance < minDistance) {
      minDistance = radiusDistance
      closestRadiusPoint = item
    }
  })
  return getCrossPointWithCircle(point, direction, closestRadiusPoint!)
}
/* æ±‚ç‚¹åœ¨åž‚ç›´æˆ–è€…æ°´å¹³æ–¹å‘ä¸Šä¸Žåœ†å½¢çš„äº¤ç‚¹ */
export const getCrossPointWithCircle = (
  point: Point,
  direction: Direction,
  node: RadiusCircle,
): Point => {
  let crossPoint: Point
  const { x, y, r } = node
  if (direction === SegmentDirection.HORIZONTAL) {
    // æ°´å¹³ï¼Œxè½´
    const crossLeft = x - Math.sqrt(r * r - (point.y - y) * (point.y - y))
    const crossRight = x + Math.sqrt(r * r - (point.y - y) * (point.y - y))
    const crossX =
      Math.abs(crossLeft - point.x) < Math.abs(crossRight - point.x)
        ? crossLeft
        : crossRight
    crossPoint = {
      x: crossX,
      y: point.y,
    }
  } else if (direction === SegmentDirection.VERTICAL) {
    // åž‚ç›´ï¼Œyè½´
    const crossTop = y - Math.sqrt(r * r - (point.x - x) * (point.x - x))
    const crossBottom = y + Math.sqrt(r * r - (point.x - x) * (point.x - x))
    const crossY =
      Math.abs(crossTop - point.y) < Math.abs(crossBottom - point.y)
        ? crossTop
        : crossBottom
    crossPoint = {
      x: point.x,
      y: crossY,
    }
  }
  return crossPoint!
}

/* åˆ¤æ–­ç‚¹æ‰€åœ¨è¾¹çš„æ–¹å‘ */
export const pointEdgeDirection = (
  point: Point,
  node: BaseNodeModel,
): Direction => {
  const dx = Math.abs(point.x - node.x)
  const dy = Math.abs(point.y - node.y)
  return dx / node.width > dy / node.height
    ? SegmentDirection.VERTICAL
    : SegmentDirection.HORIZONTAL
}

// åˆ¤æ–­çŸ©å½¢å¤–æ¡†ä¸Šä¸€ç‚¹æ˜¯å¦åœ¨çŸ©å½¢ç›´è¡Œçº¿ä¸Š
export const inStraightLineOfRect = (
  point: Point,
  node: BaseNodeModel,
): boolean => {
  const rect = node as RectNodeModel
  let isInStraight = false
  const { x, y, width, height, radius } = rect
  const rectBox = {
    minX: x - width / 2 + radius,
    maxX: x + width / 2 - radius,
    minY: y - height / 2 + radius,
    maxY: y + height / 2 - radius,
  }
  if (point.y === y + height / 2 || point.y === y - height / 2) {
    isInStraight = point.x > rectBox.minX && point.x < rectBox.maxX
  } else if (point.x === x + width / 2 || point.x === x - width / 2) {
    isInStraight = point.y > rectBox.minY && point.y < rectBox.maxY
  }
  return isInStraight
}

/* æ±‚ç‚¹åœ¨åž‚ç›´æˆ–è€…æ°´å¹³æ–¹å‘ä¸Šä¸Žæ¤­åœ†çš„äº¤ç‚¹ */
export const getCrossPointWithEllipse = (
  point: Point,
  direction: Direction,
  node: BaseNodeModel,
): Point => {
  let crossPoint: Point
  const { x, y, rx, ry } = node as EllipseNodeModel
  if (direction === SegmentDirection.HORIZONTAL) {
    // æ°´å¹³
    const crossLeft =
      x -
      Math.sqrt(rx * rx - ((point.y - y) * (point.y - y) * rx * rx) / (ry * ry))
    const crossRight =
      x +
      Math.sqrt(rx * rx - ((point.y - y) * (point.y - y) * rx * rx) / (ry * ry))
    const crossX =
      Math.abs(crossLeft - point.x) < Math.abs(crossRight - point.x)
        ? crossLeft
        : crossRight
    crossPoint = {
      x: crossX,
      y: point.y,
    }
  } else if (direction === SegmentDirection.VERTICAL) {
    // åž‚ç›´
    const crossTop =
      y -
      Math.sqrt(ry * ry - ((point.x - x) * (point.x - x) * ry * ry) / (rx * rx))
    const crossBottom =
      y +
      Math.sqrt(ry * ry - ((point.x - x) * (point.x - x) * ry * ry) / (rx * rx))
    const crossY =
      Math.abs(crossTop - point.y) < Math.abs(crossBottom - point.y)
        ? crossTop
        : crossBottom
    crossPoint = {
      x: point.x,
      y: crossY,
    }
  }
  return crossPoint!
}

/* æ±‚ç‚¹åœ¨åž‚ç›´æˆ–è€…æ°´å¹³æ–¹å‘ä¸Šä¸Žå¤šè¾¹å½¢çš„äº¤ç‚¹ */
export const getCrossPointWithPolygon = (
  point: Point,
  direction: Direction,
  node: BaseNodeModel,
): Point => {
  const { pointsPosition } = node as PolygonNodeModel
  let minDistance = Number.MAX_SAFE_INTEGER
  let crossPoint: Point
  const segments: LineSegment[] = []
  for (let i = 0; i < pointsPosition.length; i++) {
    segments.push({
      start: pointsPosition[i],
      end: pointsPosition[(i + 1) % pointsPosition.length],
    })
  }
  segments.forEach((item) => {
    const { start, end } = item
    let a = start
    let b = end
    if (start.x > end.x) {
      a = end
      b = start
    }
    let pointXY = {
      x: point.x,
      y: point.y,
    }
    // å¦‚æžœå¤šè¾¹å½¢å½“å‰çº¿æ®µæ˜¯åž‚ç›´,æ±‚äº¤ç‚¹
    if (a.x === b.x && direction === SegmentDirection.HORIZONTAL) {
      pointXY = {
        x: a.x,
        y: point.y,
      }
    }
    // å¦‚æžœå¤šè¾¹å½¢å½“å‰çº¿æ®µæ˜¯æ°´å¹³,æ±‚äº¤ç‚¹
    if (a.y === b.y && direction === SegmentDirection.VERTICAL) {
      pointXY = {
        x: point.x,
        y: a.y,
      }
    }
    // å¦‚æžœçº¿æ®µä¸æ˜¯æ°´å¹³æˆ–è€…åž‚ç›´, ä½¿ç”¨å‘é‡æ–¹ç¨‹è¿›è¡Œè®¡ç®—
    if (a.x !== b.x && a.y !== b.y) {
      const k = (b.y - a.y) / (b.x - a.x)
      const m = (a.x * b.y - b.x * a.y) / (a.x - b.x)
      if (direction === SegmentDirection.HORIZONTAL) {
        pointXY = {
          x: (point.y - m) / k,
          y: point.y,
        }
      } else if (direction === SegmentDirection.VERTICAL) {
        pointXY = {
          x: point.x,
          y: k * point.x + m,
        }
      }
    }
    // å¦‚æžœäº¤ç‚¹åœ¨çº¿æ®µä¸Š
    const inSegment = isInSegment(pointXY, start, end)
    if (inSegment) {
      const currentDistance = distance(pointXY.x, pointXY.y, point.x, point.y)
      if (currentDistance < minDistance) {
        minDistance = currentDistance
        crossPoint = pointXY
      }
    }
  })
  return crossPoint!
}

// è§„èŒƒèŠ‚ç‚¹åˆå§‹åŒ–æ•°æ®
export const pickNodeConfig = (data: NodeConfig): NodeConfig => {
  const nodeData = pick(data, [
    'id',
    'type',
    'x',
    'y',
    'text',
    'properties',
    'virtual', // åŒºåŸŸèŠ‚ç‚¹æ˜¯å¦ä¸ºdndæ·»åŠ çš„è™šæ‹ŸèŠ‚ç‚¹
    'rotate',
  ])
  return nodeData
}

/**
 * åŸºäºŽèŠ‚ç‚¹çš„è¾¹ï¼Œé‡æ–°èŽ·å–æ–°çš„èŠ‚ç‚¹
 */
export const getNodeAnchorPosition = (
  center: BaseNodeModel,
  point: Point,
  width: number,
  height: number,
) => {
  let { x, y } = center
  if (point.x > center.x) {
    x = center.x + width / 2
  } else if (point.x < center.x) {
    x = center.x - width / 2
  }
  if (point.y > center.y) {
    y = center.y + height / 2
  } else if (point.y < center.y) {
    y = center.y - height / 2
  }
  return {
    x,
    y,
  }
}

