  /**
   * å§‹åŒ–æ–‡æœ¬å±žæ€§
   */
  private formatText(data: NodeConfig): void {
    const { x, y, text } = data
    let textConfig: TextConfig = {
      value: '',
      x,
      y,
      draggable: false,
      editable: true,
    }
    if (text) {
      if (typeof text === 'string') {
        textConfig.value = text
      } else {
        textConfig = {
          ...textConfig,
          x: text.x ?? x,
          y: text.y ?? y,
          value: text.value ?? '',
        }
        if (!isUndefined(text.draggable)) {
          textConfig.draggable = text.draggable
        }
        if (!isUndefined(text.editable)) {
          textConfig.draggable = text.draggable
        }
      }
    }

    data.text = textConfig
  }

  /**
   * @overridable æ”¯æŒé‡å†™
   * è®¡ç®—èŠ‚ç‚¹ resize æ—¶
   */
  resize(resizeInfo: ResizeInfo): ResizeNodeData {
    const { width, height, deltaX, deltaY } = resizeInfo
    // ç§»åŠ¨èŠ‚ç‚¹ä»¥åŠæ–‡æœ¬å†…å®¹
    this.move(deltaX / 2, deltaY / 2)

    this.width = width
    this.height = height
    this.setProperties({
      width,
      height,
    })

    return this.getData()
  }

  // TODO: ç­‰æ¯”ä¾‹ç¼©æ”¾
  proportionalResize() {}

  /**
   * èŽ·å–è¢«ä¿å­˜æ—¶è¿”å›žçš„æ•°æ®
   * @overridable æ”¯æŒé‡å†™
   */
  getData(): NodeData {
    const { x, y, value } = this.text
    let { properties } = this
    if (isObservable(properties)) {
      properties = toJS(properties)
    }
    const data: NodeData = {
      id: this.id,
      type: this.type,
      x: this.x,
      y: this.y,
      properties,
    }
    if (this.rotate) {
      data.rotate = this.rotate
    }
    if (this.graphModel.overlapMode === OverlapMode.INCREASE) {
      data.zIndex = this.zIndex
    }
    if (value) {
      data.text = {
        x,
        y,
        value,
      }
    }
    return data
  }

