import { cloneDeep, map } from 'lodash-es'
import { computed, observable } from 'mobx'
import BaseNodeModel from './BaseNodeModel'
import GraphModel from '../GraphModel'
import LogicFlow from '../../LogicFlow'
import { ModelType } from '../../constant'
import { ResizeControl } from '../../view/Control'
import Point = LogicFlow.Point
import PointTuple = LogicFlow.PointTuple
import NodeConfig = LogicFlow.NodeConfig
import ResizeInfo = ResizeControl.ResizeInfo
import ResizeNodeData = ResizeControl.ResizeNodeData
import { normalizePolygon } from '../../util'

export type IPolygonNodeProperties = {
  points?: PointTuple[]
  width?: number
  height?: number
  style?: LogicFlow.CommonTheme
  textStyle?: LogicFlow.CommonTheme

  [key: string]: any
}

export class PolygonNodeModel extends BaseNodeModel {
  modelType = ModelType.POLYGON_NODE
  @observable points: PointTuple[] = [
    [50, 0],
    [100, 50],
    [50, 100],
    [0, 50], // è±å½¢
    // [0,100], [50,25], [50,75], [100,0] // é—ªç”µ
    // [100, 10],
    // [40, 198],
    // [190, 78],
    // [10, 78],
    // [160, 198], // äº”è§’æ˜Ÿ
  ]
  @observable properties: IPolygonNodeProperties = {}

  constructor(data: NodeConfig, graphModel: GraphModel) {
    super(data, graphModel)
    this.properties = data.properties || {}

    this.setAttributes()
  }

  setAttributes() {
    super.setAttributes()

    const { points, width, height } = this.properties
    // DONE: å¦‚æžœè®¾ç½®äº† pointsï¼Œåˆè®¾ç½®äº†èŠ‚ç‚¹çš„å®½é«˜ï¼Œåˆ™éœ€è¦åšå¦‚ä¸‹æ“ä½œ
    // 1. å°† points çš„ä½ç½®ç½®é›¶ï¼Œå³å°†å›¾å½¢å‘åŽŸç‚¹ç§»åŠ¨ï¼ˆæ‰¾åˆ° points ä¸­ x,y çš„æœ€å°å€¼ï¼Œæ‰€æœ‰åæ ‡å€¼å‡æŽ‰è¯¥å€¼ï¼‰
    // 2. æŒ‰å®½é«˜çš„æ¯”ä¾‹é‡æ–°è®¡ç®—æœ€æ–°çš„ points
    // if (points) {
    //   this.points = points
    // }
    const nextPoints = points || this.points
    this.points = normalizePolygon(nextPoints, width, height)
  }

  getNodeStyle() {
    const style = super.getNodeStyle()
    const {
      graphModel: {
        theme: { polygon },
      },
    } = this
    const { style: customStyle = {} } = this.properties
    return {
      ...style,
      ...cloneDeep(polygon),
      ...cloneDeep(customStyle),
    }
  }

  /**
   * ç”±äºŽå¤§å¤šæ•°æƒ…å†µä¸‹ï¼Œæˆ‘ä»¬åˆå§‹åŒ–æ‹¿åˆ°çš„å¤šè¾¹å½¢åæ ‡éƒ½æ˜¯åŸºäºŽåŽŸç‚¹çš„ï¼ˆä¾‹å¦‚ç»˜å›¾å·¥å…·åˆ°å¤„çš„svgï¼‰ã€‚
   * åœ¨logicflowä¸­å¯¹å¤šè¾¹å½¢è¿›è¡Œç§»åŠ¨ï¼Œæˆ‘ä»¬ä¸éœ€è¦åŽ»æ›´æ–°pointsï¼Œ
   * è€Œæ˜¯åŽ»æ›´æ–°å¤šè¾¹å½¢ä¸­å¿ƒç‚¹å³å¯ã€‚
   */
  @computed get pointsPosition(): Point[] {
    const { x, y, width, height } = this
    return this.points.map((item) => ({
      x: item[0] + x - width / 2,
      y: item[1] + y - height / 2,
    }))
  }

  @computed get width(): number {
    let min = Number.MAX_SAFE_INTEGER
    let max = Number.MIN_SAFE_INTEGER
    this.points.forEach(([x]) => {
      if (x < min) {
        min = x
      }
      if (x > max) {
        max = x
      }
    })
    return max - min
  }

  @computed get height(): number {
    let min = Number.MAX_SAFE_INTEGER
    let max = Number.MIN_SAFE_INTEGER
    this.points.forEach(([, y]) => {
      if (y < min) {
        min = y
      }
      if (y > max) {
        max = y
      }
    })
    return max - min
  }

  getDefaultAnchor() {
    const { x, y, width, height, points } = this
    return points.map(([x1, y1], idx) => ({
      x: x + x1 - width / 2,
      y: y + y1 - height / 2,
      id: `${this.id}_${idx}`,
    }))
  }

  resize(resizeInfo: ResizeInfo): ResizeNodeData {
    const { width, height, deltaX, deltaY } = resizeInfo
    // ç§»åŠ¨èŠ‚ç‚¹ä»¥åŠæ–‡æœ¬å†…å®¹
    this.move(deltaX / 2, deltaY / 2)

    const nextPoints: PointTuple[] = map(this.points, ([x, y]) => [
      (x * width) / this.width,
      (y * height) / this.height,
    ])
    this.points = nextPoints

    this.properties.points = nextPoints
    // this.setProperties({
    //   points: toJS(nextPoints),
    // })

    return this.getData()
  }
}

export default PolygonNodeModel
