import { createElement as h, Component } from 'preact/compat'
import { reaction, IReactionDisposer } from 'mobx'
import { map } from 'lodash-es'
import Anchor from '../Anchor'
import { BaseText } from '../text'
import LogicFlow from '../../LogicFlow'
import { GraphModel, BaseNodeModel, Model } from '../../model'
import { ElementState, EventType } from '../../constant'
import {
  StepDrag,
  snapToGrid,
  isIe,
  isMultipleSelect,
  cancelRaf,
  createRaf,
  TranslateMatrix,
  IDragParams,
  // RotateMatrix,
} from '../../util'
import RotateControlPoint from '../Rotate'
import ResizeControlGroup from '../Control'

type IProps = {
  model: BaseNodeModel
  graphModel: GraphModel
}

type IState = {
  isDragging?: boolean
}

export abstract class BaseNode<P extends IProps> extends Component<P, IState> {
  static isObserved: boolean = false
  static extendsKey?: string

  t: any
  moveOffset?: LogicFlow.OffsetData

  stepDrag: StepDrag
  mouseUpDrag?: boolean
  startTime?: number
  modelDisposer: IReactionDisposer

  constructor(props: IProps) {
    super()
    const {
      graphModel: { gridSize, eventCenter },
      model,
    } = props
    // ä¸åœ¨æž„é€ å‡½æ•°ä¸­åˆ¤æ–­ï¼Œå› ä¸ºeditConfigå¯èƒ½ä¼šè¢«åŠ¨æ€æ”¹å˜
    this.stepDrag = new StepDrag({
      onDragStart: this.onDragStart,
      onDragging: this.onDragging,
      onDragEnd: this.onDragEnd,
      step: gridSize,
      eventType: 'NODE',
      isStopPropagation: false,
      eventCenter,
      model,
    })
    // https://github.com/didi/LogicFlow/issues/1370
    // å½“ä½¿ç”¨æ’¤é”€åŠŸèƒ½ï¼šLogicFlow.undo()æ—¶ï¼Œä¼šé‡æ–°åˆå§‹åŒ–æ‰€æœ‰modelæ•°æ®ï¼Œå³LogicFlow.undo()æ—¶ä¼šæ–°æž„å»ºä¸€ä¸ªmodelå¯¹è±¡
    // ä½†æ˜¯this.stepDragå¹¶ä¸ä¼šé‡æ–°åˆ›å»º
    // å¯¼è‡´this.stepDragæŒæœ‰çš„modelå¹¶æ²¡æœ‰é‡æ–°èµ‹å€¼ï¼Œå› ä¸ºä¹‹å‰çš„åšæ³•æ˜¯æž„é€ å‡½æ•°ä¸­ä¼ å…¥ä¸€ä¸ªmodelå¯¹è±¡
    // ä½¿ç”¨mobxçš„reactionç›‘å¬èƒ½åŠ›ï¼Œå¦‚æžœthis.props.modelå‘ç”Ÿå˜åŒ–ï¼Œåˆ™è¿›è¡Œthis.stepDrag.setModel()æ“ä½œ
    this.modelDisposer = reaction(
      () => this.props,
      (newProps) => {
        if (newProps && newProps.model) {
          this.stepDrag.setModel(newProps.model)
        }
      },
    )
  }

  componentWillUnmount() {
    if (this.modelDisposer) {
      this.modelDisposer()
    }
  }

  componentDidMount() {}

  componentDidUpdate() {}

  abstract getShape(): h.JSX.Element | null

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  getAnchorShape(_anchorData?: Model.AnchorConfig): h.JSX.Element | null {
    return null
  }

  getAnchors() {
    const { model, graphModel } = this.props
    const { isSelected, isHitable, isDragging, isShowAnchor } = model
    if (isHitable && (isSelected || isShowAnchor) && !isDragging) {
      return map(model.anchors, (anchor, index) => {
        const edgeStyle = model.getAnchorLineStyle(anchor)
        const style = model.getAnchorStyle(anchor)
        return (
          <Anchor
            anchorData={anchor}
            node={this}
            style={style}
            edgeStyle={edgeStyle}
            anchorIndex={index}
            nodeModel={model}
            graphModel={graphModel}
            setHoverOff={this.setHoverOff}
          />
        )
      })
    }
    return []
  }

  getRotateControl() {
    const { model, graphModel } = this.props
    const { isSelected, isHitable, enableRotate, isHovered } = model
    const style = model.getRotateControlStyle()
    if (isHitable && (isSelected || isHovered) && enableRotate) {
      return (
        <RotateControlPoint
          graphModel={graphModel}
          nodeModel={model}
          eventCenter={graphModel.eventCenter}
          style={style}
        />
      )
    }
  }

  getResizeControl(): h.JSX.Element | null {
    const { model, graphModel } = this.props
    const { isSelected, isHitable, enableResize, isHovered } = model
    const style = model.getResizeControlStyle()
    if (isHitable && (isSelected || isHovered) && enableResize) {
      return (
        <ResizeControlGroup
          style={style}
          model={model}
          graphModel={graphModel}
        />
      )
    }
    return null
  }

  getText(): h.JSX.Element | null {
    const { model, graphModel } = this.props
    // æ–‡æœ¬è¢«ç¼–è¾‘çš„æ—¶å€™ï¼Œæ˜¾ç¤ºç¼–è¾‘æ¡†ï¼Œä¸æ˜¾ç¤ºæ–‡æœ¬ã€‚
    if (model.state === ElementState.TEXT_EDIT) {
      return null
    }
    if (model.text) {
      const { editConfigModel } = graphModel
      let draggable = false
      if (model.text.draggable || editConfigModel.nodeTextDraggable) {
        draggable = true
      }
      return (
        <BaseText
          editable={
            editConfigModel.nodeTextEdit && (model.text.editable ?? true)
          }
          model={model}
          graphModel={graphModel}
          draggable={draggable}
        />
      )
    }
    return null
  }

  getStateClassName() {
    const {
      model: { state, isDragging, isSelected },
    } = this.props
    let className = 'lf-node'
    switch (state) {
      case ElementState.ALLOW_CONNECT:
        className += ' lf-node-allow'
        break
      case ElementState.NOT_ALLOW_CONNECT:
        className += ' lf-node-not-allow'
        break
      default:
        className += ' lf-node-default'
        break
    }
    if (isDragging) {
      className += ' lf-isDragging'
    }
    if (isSelected) {
      className += ' lf-node-selected'
    }
    return className
  }

  onDragStart = ({ event }: Partial<IDragParams>) => {
    const { model, graphModel } = this.props
    if (event) {
      const {
        canvasOverlayPosition: { x, y },
      } = graphModel.getPointByClient({
        x: event.clientX,
        y: event.clientY,
      })
      this.moveOffset = {
        dx: model.x - x,
        dy: model.y - y,
      }
    }
  }
  onDragging = ({ event }: IDragParams) => {
    const { model, graphModel } = this.props
    // const { isDragging } = model;
    const {
      editConfigModel: { stopMoveGraph, autoExpand },
      transformModel,
      selectNodes,
      width,
      height,
      gridSize,
    } = graphModel
    model.isDragging = true
    const { clientX, clientY } = event!
    let {
      canvasOverlayPosition: { x, y },
    } = graphModel.getPointByClient({
      x: clientX,
      y: clientY,
    })
    const [x1, y1] = transformModel.CanvasPointToHtmlPoint([x, y])
    // 1. è€ƒè™‘ç”»å¸ƒè¢«ç¼©æ”¾
    // 2. è€ƒè™‘é¼ æ ‡ä½ç½®ä¸å†èŠ‚ç‚¹ä¸­å¿ƒ
    x = x + (this.moveOffset?.dx ?? 0)
    y = y + (this.moveOffset?.dy ?? 0)
    // å°†x, yç§»åŠ¨åˆ°gridä¸Š
    x = snapToGrid(x, gridSize)
    y = snapToGrid(y, gridSize)
    if (!width || !height) {
      graphModel.moveNode2Coordinate(model.id, x, y)
      return
    }
    const isOutCanvas = x1 < 0 || y1 < 0 || x1 > width || y1 > height
    if (autoExpand && !stopMoveGraph && isOutCanvas) {
      // é¼ æ ‡è¶…å‡ºç”»å¸ƒåŽçš„æ‹–åŠ¨ï¼Œä¸å¤„ç†ï¼Œè€Œæ˜¯è®©ä¸Šä¸€æ¬¡setIntervalæŒç»­æ»šåŠ¨ç”»å¸ƒ
      return
    }
    // å–èŠ‚ç‚¹å·¦ä¸Šè§’å’Œå³ä¸‹è§’ï¼Œè®¡ç®—èŠ‚ç‚¹ç§»åŠ¨æ˜¯å¦è¶…å‡ºèŒƒå›´
    const [leftTopX, leftTopY] = transformModel.CanvasPointToHtmlPoint([
      x - model.width / 2,
      y - model.height / 2,
    ])
    const [rightBottomX, rightBottomY] = transformModel.CanvasPointToHtmlPoint([
      x + model.width / 2,
      y + model.height / 2,
    ])
    const size: number = Math.max(gridSize, 20)
    let nearBoundary: LogicFlow.PointTuple | [] = []
    if (leftTopX < 0) {
      nearBoundary = [size, 0]
    } else if (rightBottomX > graphModel.width) {
      nearBoundary = [-size, 0]
    } else if (leftTopY < 0) {
      nearBoundary = [0, size]
    } else if (rightBottomY > graphModel.height) {
      nearBoundary = [0, -size]
    }
    if (this.t) {
      cancelRaf(this.t)
    }

    model.transform = new TranslateMatrix(-x, -y)
      .rotate(model.rotate)
      .translate(x, y)
      .toString()

    let moveNodes = selectNodes.map((node) => node.id)
    // æœªè¢«é€‰ä¸­çš„èŠ‚ç‚¹ä¹Ÿå¯ä»¥æ‹–åŠ¨
    if (moveNodes.indexOf(model.id) === -1) {
      moveNodes = [model.id]
    }
    if (nearBoundary.length > 0 && !stopMoveGraph && autoExpand) {
      this.t = createRaf(() => {
        const [translateX, translateY] = nearBoundary
        transformModel.translate(translateX ?? 0, translateY ?? 0)
        const deltaX = -(translateX ?? 0) / transformModel.SCALE_X
        const deltaY = -(translateY ?? 0) / transformModel.SCALE_X
        graphModel.moveNodes(moveNodes, deltaX, deltaY)
      })
    } else {
      graphModel.moveNodes(moveNodes, x - model.x, y - model.y)
    }
  }
  onDragEnd = () => {
    if (this.t) {
      cancelRaf(this.t)
    }
    const { model } = this.props
    model.isDragging = false
  }
  handleMouseUp = () => {
    const { model } = this.props
    this.mouseUpDrag = model.isDragging
  }
  handleClick = (e: MouseEvent) => {
    // èŠ‚ç‚¹æ‹–æ‹½è¿›ç”»å¸ƒä¹‹åŽï¼Œä¸è§¦å‘clickäº‹ä»¶ç›¸å…³emit
    // ç‚¹æ‹–æ‹½è¿›ç”»å¸ƒæ²¡æœ‰è§¦å‘mousedownäº‹ä»¶ï¼Œæ²¡æœ‰startTimeï¼Œç”¨è¿™ä¸ªå€¼åšåŒºåˆ†
    const isDragging = this.mouseUpDrag === false
    if (!this.startTime) return
    const { model, graphModel } = this.props
    if (!isDragging) return // å¦‚æžœæ˜¯æ‹–æ‹½, ä¸è§¦å‘clickäº‹ä»¶ã€‚
    // èŠ‚ç‚¹æ•°æ®ï¼Œå¤šä¸ºäº‹ä»¶å¯¹è±¡æ•°æ®æŠ›å‡º
    const nodeData = model.getData()
    const position = graphModel.getPointByClient({
      x: e.clientX,
      y: e.clientY,
    })

    // TODO: è¿™é‡ŒåŠ å…¥äº† isSelected ä¸Ž isMultipleï¼Œä¸»è¦æ˜¯ä¸º group æ’ä»¶åšçš„åŠ å¼ºï¼Œæœ‰ç§è¢«æ’ä»¶å¤ºèˆçš„æ„Ÿè§‰
    const eventOptions = {
      data: nodeData,
      e,
      position,
      isSelected: false,
      isMultiple: false,
    }

    const isRightClick = e.button === 2
    // è¿™é‡Œ IE 11ä¸èƒ½æ­£ç¡®æ˜¾ç¤º
    const isDoubleClick = e.detail === 2

    // åˆ¤æ–­æ˜¯å¦æœ‰å³å‡»ï¼Œå¦‚æžœæœ‰å³å‡»åˆ™å–æ¶ˆç‚¹å‡»äº‹ä»¶è§¦å‘
    if (isRightClick) return

    const { editConfigModel } = graphModel
    // åœ¨multipleSelect toolç¦ç”¨çš„æƒ…å†µä¸‹ï¼Œå…è®¸å–æ¶ˆé€‰ä¸­èŠ‚ç‚¹
    const isMultiple = isMultipleSelect(e, editConfigModel)
    eventOptions.isMultiple = isMultiple
    if (model.isSelected && !isDoubleClick && isMultiple) {
      eventOptions.isSelected = false
      model.setSelected(false)
    } else {
      graphModel.selectNodeById(model.id, isMultiple)
      eventOptions.isSelected = true
      this.toFront()
    }

    // ä¸æ˜¯åŒå‡»çš„ï¼Œé»˜è®¤éƒ½æ˜¯å•å‡»
    if (isDoubleClick) {
      if (editConfigModel.nodeTextEdit && model.text.editable) {
        model.setSelected(false)
        graphModel.setElementStateById(model.id, ElementState.TEXT_EDIT)
      }
      graphModel.eventCenter.emit(EventType.NODE_DBCLICK, eventOptions)
    } else {
      graphModel.eventCenter.emit(EventType.ELEMENT_CLICK, eventOptions)
      graphModel.eventCenter.emit(EventType.NODE_CLICK, eventOptions)
    }
  }
  handleContextMenu = (ev: MouseEvent) => {
    ev.preventDefault()
    const { model, graphModel } = this.props
    // èŠ‚ç‚¹æ•°æ®ï¼Œå¤šä¸ºäº‹ä»¶å¯¹è±¡æ•°æ®æŠ›å‡º
    const nodeData = model.getData()

    const position = graphModel.getPointByClient({
      x: ev.clientX,
      y: ev.clientY,
    })
    graphModel.setElementStateById(
      model.id,
      ElementState.SHOW_MENU,
      position.domOverlayPosition,
    )
    if (!model.isSelected) {
      graphModel.selectNodeById(model.id)
    }
    graphModel.eventCenter.emit(EventType.NODE_CONTEXTMENU, {
      data: nodeData,
      e: ev,
      position,
    })
    this.toFront()
  }
  handleMouseDown = (ev: MouseEvent) => {
    const { model, graphModel } = this.props
    this.startTime = new Date().getTime()
    const { editConfigModel } = graphModel
    if (editConfigModel.adjustNodePosition && model.draggable) {
      this.stepDrag && this.stepDrag.handleMouseDown(ev)
    }
  }
  // ä¸ºä»€ä¹ˆå°†hoverçŠ¶æ€æ”¾åˆ°modelä¸­ï¼Ÿ
  // å› ä¸ºè‡ªå®šä¹‰èŠ‚ç‚¹çš„æ—¶å€™ï¼Œå¯èƒ½ä¼šåŸºäºŽhoverçŠ¶æ€è‡ªå®šä¹‰ä¸åŒçš„æ ·å¼ã€‚
  setHoverOn = (ev: MouseEvent) => {
    const { model, graphModel } = this.props
    if (model.isHovered) return
    const nodeData = model.getData()
    model.setHovered(true)
    graphModel.eventCenter.emit(EventType.NODE_MOUSEENTER, {
      data: nodeData,
      e: ev,
    })
  }
  setHoverOff = (ev: MouseEvent) => {
    const { model, graphModel } = this.props
    const nodeData = model.getData()
    if (!model.isHovered) return
    model.setHovered(false)
    graphModel.eventCenter.emit(EventType.NODE_MOUSELEAVE, {
      data: nodeData,
      e: ev,
    })
  }
  onMouseOut = (ev: MouseEvent) => {
    if (isIe) {
      this.setHoverOff(ev)
    }
  }

  /**
   *  @overridable æ”¯æŒé‡å†™, èŠ‚ç‚¹ç½®é¡¶ï¼Œå¯ä»¥è¢«æŸäº›ä¸éœ€è¦ç½®é¡¶çš„èŠ‚ç‚¹é‡å†™ï¼Œå¦‚groupèŠ‚ç‚¹ã€‚
   */
  toFront() {
    const { model, graphModel } = this.props
    if (model.autoToFront) {
      graphModel.toFront(model.id)
    }
  }

  render() {
    const { model, graphModel } = this.props
    const {
      editConfigModel: {
        hideAnchors,
        adjustNodePosition,
        allowRotate,
        allowResize,
      },
      gridSize,
      transformModel: { SCALE_X },
    } = graphModel
    const { isHitable, draggable, transform } = model
    const { className = '', ...restAttributes } = model.getOuterGAttributes()
    const nodeShapeInner = (
      <g className="lf-node-content">
        <g transform={transform}>
          {this.getShape()}
          {this.getText()}
          {allowRotate && this.getRotateControl()}
          {allowResize && this.getResizeControl()}
        </g>
        {!hideAnchors && this.getAnchors()}
      </g>
    )
    let nodeShape: h.JSX.Element
    if (!isHitable) {
      nodeShape = (
        <g
          className={`${this.getStateClassName()} ${className}`}
          {...restAttributes}
        >
          {nodeShapeInner}
        </g>
      )
    } else {
      if (adjustNodePosition && draggable) {
        this.stepDrag.setStep(gridSize * SCALE_X)
      }
      nodeShape = (
        <g
          className={`${this.getStateClassName()} ${className}`}
          onMouseDown={this.handleMouseDown}
          onMouseUp={this.handleMouseUp}
          onClick={this.handleClick}
          onMouseEnter={this.setHoverOn}
          onMouseOver={this.setHoverOn}
          onMouseLeave={this.setHoverOff}
          onMouseOut={this.onMouseOut}
          onContextMenu={this.handleContextMenu}
          {...restAttributes}
        >
          {nodeShapeInner}
        </g>
      )
    }
    return nodeShape
  }
}

export default BaseNode
