import { createElement as h, Component, createRef } from 'preact/compat'
import { Circle } from '../shape'
import { LineText } from '../text'
import LogicFlow from '../../LogicFlow'
import { GraphModel, BaseEdgeModel, PolylineEdgeModel } from '../../model'
import { ElementState, EventType, ModelType } from '../../constant'
import {
  isMultipleSelect,
  getClosestPointOfPolyline,
  degrees,
  getThetaOfVector,
} from '../../util'
import AdjustPoint, { AdjustType } from './AdjustPoint'

import ArrowInfo = LogicFlow.ArrowInfo
import Point = LogicFlow.Point

type IProps = {
  model: BaseEdgeModel
  graphModel: GraphModel
}
export type IEdgeState = {
  hover: boolean
}

export abstract class BaseEdge<P extends IProps> extends Component<
  P,
  IEdgeState
> {
  static isObserved: boolean = false
  static extendsKey?: string

  startTime?: number
  contextMenuTime?: number
  clickTimer?: number
  textRef = createRef()

  constructor() {
    super()
  }

  /**
   * ä¸æ”¯æŒé‡å†™ï¼Œè¯·ä½¿ç”¨getEdge
   */
  getShape() {
    return <g>{this.getEdge()}</g>
  }

  /**
   * @deprecated è¯·ä½¿ç”¨model.getTextStyle
   */
  getTextStyle() {}

  /**
   * @overridable å¯é‡å†™ï¼Œè‡ªå®šä¹‰è¾¹æ–‡æœ¬DOM
   */
  getText(): h.JSX.Element | null {
    const { model, graphModel } = this.props
    // æ–‡æœ¬è¢«ç¼–è¾‘çš„æ—¶å€™ï¼Œæ˜¾ç¤ºç¼–è¾‘æ¡†ï¼Œä¸æ˜¾ç¤ºæ–‡æœ¬ã€‚
    if (model.state === ElementState.TEXT_EDIT) {
      return null
    }
    let draggable = false
    const { editConfigModel } = graphModel
    if (model.text.draggable || editConfigModel.edgeTextDraggable) {
      draggable = true
    }
    return (
      <LineText
        ref={this.textRef}
        editable={editConfigModel.edgeTextEdit && model.text.editable}
        model={model}
        graphModel={graphModel}
        draggable={draggable}
      />
    )
  }

  /**
   * @deprecated
   */
  getArrowInfo(): ArrowInfo {
    const { model } = this.props
    const { startPoint, endPoint, isSelected } = model
    const { hover } = this.state as IEdgeState
    return {
      start: startPoint,
      end: endPoint,
      hover,
      isSelected,
    }
  }

  getLastTwoPoints(): [Point, Point] {
    const { model } = this.props
    const { startPoint, endPoint } = model
    return [startPoint, endPoint]
  }

  /**
   * @deprecated è¯·ä½¿ç”¨model.getArrowStyle
   */
  getArrowStyle() {
    console.error(
      'getArrowStyle is deprecated in 1.2.0, please use model.getArrowStyle',
    )
    return null
  }

  /**
   * å®šä¹‰è¾¹çš„ç®­å¤´ï¼Œä¸æ”¯æŒé‡å†™ã€‚è¯·ä½¿ç”¨getStartArrowå’ŒgetEndArrow
   */
  private getArrow(): h.JSX.Element | null {
    const { model } = this.props
    const { id } = model
    const { refY = 0, refX = 2 } = model.getArrowStyle()
    const [start, end] = this.getLastTwoPoints()
    let theta: string | number = 'auto'
    if (start !== null && end !== null) {
      theta = degrees(
        getThetaOfVector({
          x: end.x - start.x,
          y: end.y - start.y,
          z: 0,
        }),
      )
    }
    return (
      <g>
        <defs>
          <marker
            id={`marker-start-${id}`}
            refX={-refX}
            refY={refY}
            overflow="visible"
            orient="auto"
            markerUnits="userSpaceOnUse"
          >
            {this.getStartArrow()}
          </marker>
          <marker
            id={`marker-end-${id}`}
            refX={refX}
            refY={refY}
            overflow="visible"
            orient={theta}
            markerUnits="userSpaceOnUse"
            // transform={`rotate(${degrees(theta)})`}
          >
            {this.getEndArrow()}
          </marker>
        </defs>
      </g>
    )
  }

  /**
   * @overridable å¯é‡å†™ï¼Œè‡ªå®šä¹‰è¾¹èµ·ç‚¹ç®­å¤´å½¢çŠ¶ã€‚
   * @example
   * getStartArrow() {
   *  const { model } = this.props;
   *  const { stroke, strokeWidth, offset, verticalLength } = model.getArrowStyle();
   *  return (
   *    h('path', {
   *      d: ''
   *    })
   *  )
   * }
   */
  getStartArrow(): h.JSX.Element | null {
    return <path />
  }

  /**
   * @overridable å¯é‡å†™ï¼Œè‡ªå®šä¹‰è¾¹ç»ˆç‚¹ç®­å¤´å½¢çŠ¶ã€‚
   * @example
   * getEndArrow() {
   *  const { model } = this.props;
   *  const { stroke, strokeWidth, offset, verticalLength } = model.getArrowStyle();
   *  return (
   *    h('path', {
   *      d: ''
   *    })
   *  )
   * }
   */
  getEndArrow(): h.JSX.Element | null {
    const { model } = this.props
    const { stroke, strokeWidth, offset, verticalLength } =
      model.getArrowStyle()
    return (
      <path
        stroke={stroke}
        fill={stroke}
        strokeWidth={strokeWidth}
        transform="rotate(180)"
        d={`M 0 0 L ${offset} -${verticalLength} L ${offset} ${verticalLength} Z`}
      />
    )
  }

  /**
   * @overridable å¯é‡å†™ï¼Œè‡ªå®šä¹‰è°ƒæ•´è¾¹è¿žæŽ¥èŠ‚ç‚¹å½¢çŠ¶ã€‚åœ¨å¼€å¯äº†adjustEdgeStartAndEndçš„æ—¶å€™ï¼Œä¼šæ˜¾ç¤ºè°ƒæ•´ç‚¹ã€‚
   * @param x è°ƒæ•´ç‚¹xåæ ‡
   * @param y è°ƒæ•´ç‚¹yåæ ‡
   * @param model
   * @example
   * getAdjustPointShape(x, y) {
   *  const { model } = this.props;
   *  const style = model.getAdjustPointStyle();
   *  return (
   *    h('circle', {
   *      ...style,
   *     x,
   *     y
   *    })
   *  )
   * }
   */
  getAdjustPointShape(
    x: number,
    y: number,
    model: BaseEdgeModel,
  ): h.JSX.Element | null {
    const style = model.getAdjustPointStyle()
    return (
      <Circle
        className="lf-edge-adjust-point"
        {...style}
        {...{
          x,
          y,
        }}
      />
    )
  }

  /**
   * ä¸æ”¯æŒé‡å†™ã€‚è¯·ä½¿ç”¨getAdjustPointShape
   */
  private getAdjustPoints() {
    const { model, graphModel } = this.props
    const start = model.getAdjustStart()
    const end = model.getAdjustEnd()
    return (
      <g>
        <AdjustPoint
          type={AdjustType.SOURCE}
          {...start}
          getAdjustPointShape={this.getAdjustPointShape}
          edgeModel={model}
          graphModel={graphModel}
        />
        <AdjustPoint
          type={AdjustType.TARGET}
          {...end}
          getAdjustPointShape={this.getAdjustPointShape}
          edgeModel={model}
          graphModel={graphModel}
        />
      </g>
    )
  }

  /**
   * @deprecated
   */
  getAnimation() {
    console.error(
      'getAnimation is deprecated in 1.2.0, please use model.getEdgeAnimationStyle',
    )
  }

  /**
   * @overridable å¯é‡å†™ï¼Œåœ¨å®Œå…¨è‡ªå®šä¹‰è¾¹çš„æ—¶å€™ï¼Œå¯ä»¥é‡å†™æ­¤æ–¹æ³•ï¼Œæ¥è‡ªå®šä¹‰è¾¹çš„é€‰åŒºã€‚
   */
  public getAppendWidth() {
    return <g />
  }

  /**
   * ä¸å»ºè®®é‡å†™ï¼Œæ­¤æ–¹æ³•ä¸ºæ‰©å¤§è¾¹é€‰åŒºï¼Œæ–¹ä¾¿ç”¨æˆ·ç‚¹å‡»é€‰ä¸­è¾¹ã€‚
   * å¦‚æžœéœ€è¦è‡ªå®šä¹‰è¾¹é€‰åŒºï¼Œè¯·ä½¿ç”¨getAppendWidthã€‚
   */
  getAppend() {
    return <g className="lf-edge-append">{this.getAppendWidth()}</g>
  }

  /**
   * ä¸æ”¯æŒé‡å†™ï¼Œå¦‚æžœæƒ³è¦åŸºäºŽhoverçŠ¶æ€è®¾ç½®ä¸åŒçš„æ ·å¼ï¼Œè¯·åœ¨modelä¸­ä½¿ç”¨isHoveredå±žæ€§ã€‚
   */
  handleHover = (hovered: boolean, ev: MouseEvent) => {
    const {
      model,
      graphModel: { eventCenter },
    } = this.props
    model.setHovered(hovered)
    const eventName = hovered
      ? EventType.EDGE_MOUSEENTER
      : EventType.EDGE_MOUSELEAVE
    const nodeData = model.getData()
    eventCenter.emit(eventName, {
      data: nodeData,
      e: ev,
    })
  }
  /**
   * ä¸æ”¯æŒé‡å†™ï¼Œå¦‚æžœæƒ³è¦åŸºäºŽhoverçŠ¶æ€è®¾ç½®ä¸åŒçš„æ ·å¼ï¼Œè¯·åœ¨modelä¸­ä½¿ç”¨isHoveredå±žæ€§ã€‚
   */
  setHoverOn = (ev: MouseEvent) => {
    // ! hoverå¤šæ¬¡è§¦å‘, onMouseOver + onMouseEnter
    const {
      model: { isHovered },
    } = this.props
    if (isHovered) return
    this.textRef && this.textRef.current && this.textRef.current.setHoverOn()
    this.handleHover(true, ev)
  }
  /**
   * ä¸æ”¯æŒé‡å†™ï¼Œå¦‚æžœæƒ³è¦åŸºäºŽhoverçŠ¶æ€è®¾ç½®ä¸åŒçš„æ ·å¼ï¼Œè¯·åœ¨modelä¸­ä½¿ç”¨isHoveredå±žæ€§ã€‚
   */
  setHoverOff = (ev: MouseEvent) => {
    const {
      model: { isHovered },
    } = this.props
    if (!isHovered) return
    this.textRef && this.textRef.current && this.textRef.current.setHoverOff()
    this.handleHover(false, ev)
  }
  /**
   * ä¸æ”¯æŒé‡å†™ï¼Œå¦‚æžœæƒ³è¦åŸºäºŽcontextmenuäº‹ä»¶åšå¤„ç†ï¼Œè¯·ç›‘å¬edge:contextmenuäº‹ä»¶ã€‚
   */
  handleContextMenu = (ev: MouseEvent) => {
    ev.preventDefault()
    // èŠ‚ç‚¹å³å‡»ä¹Ÿä¼šè§¦å‘æ—¶é—´ï¼ŒåŒºåˆ†å³å‡»å’Œç‚¹å‡»(mouseup)
    this.contextMenuTime = new Date().getTime()
    if (this.clickTimer) {
      clearTimeout(this.clickTimer)
    }
    const { model, graphModel } = this.props
    const position = graphModel.getPointByClient({
      x: ev.clientX,
      y: ev.clientY,
    })
    graphModel.setElementStateById(
      model.id,
      ElementState.SHOW_MENU,
      position.domOverlayPosition,
    )
    this.toFront()
    if (!model.isSelected) {
      graphModel.selectEdgeById(model.id)
    }
    // è¾¹æ•°æ®
    const edgeData = model?.getData()
    graphModel.eventCenter.emit(EventType.EDGE_CONTEXTMENU, {
      data: edgeData,
      e: ev,
      position,
    })
  }
  /**
   * ä¸æ”¯æŒé‡å†™
   */
  handleMouseDown = (e: MouseEvent) => {
    e.stopPropagation()
    this.startTime = new Date().getTime()
  }
  /**
   * ä¸æ”¯æŒé‡å†™
   */
  handleMouseUp = (e: MouseEvent) => {
    if (!this.startTime) return
    const time = new Date().getTime() - this.startTime
    if (time > 200) return // äº‹ä»¶å¤§äºŽ200msï¼Œè®¤ä¸ºæ˜¯æ‹–æ‹½ã€‚
    const isRightClick = e.button === 2
    if (isRightClick) return
    // è¿™é‡Œ IE 11ä¸èƒ½æ­£ç¡®æ˜¾ç¤º
    const isDoubleClick = e.detail === 2
    const { model, graphModel } = this.props
    const edgeData = model?.getData()
    const position = graphModel.getPointByClient({
      x: e.clientX,
      y: e.clientY,
    })
    if (isDoubleClick) {
      const { editConfigModel, textEditElement } = graphModel
      // å½“å‰è¾¹æ­£åœ¨ç¼–è¾‘ï¼Œéœ€è¦å…ˆé‡ç½®çŠ¶æ€æ‰èƒ½å˜æ›´æ–‡æœ¬æ¡†ä½ç½®
      if (textEditElement && textEditElement.id === model.id) {
        graphModel.setElementStateById(model.id, ElementState.DEFAULT)
      }
      // è¾¹æ–‡æ¡ˆå¯ç¼–è¾‘çŠ¶æ€ï¼Œæ‰å¯ä»¥è¿›è¡Œæ–‡æ¡ˆç¼–è¾‘
      if (editConfigModel.edgeTextEdit && model.text.editable) {
        graphModel.setElementStateById(model.id, ElementState.TEXT_EDIT)
      }
      if (model.modelType === ModelType.POLYLINE_EDGE) {
        const polylineEdgeModel = model as PolylineEdgeModel
        const {
          canvasOverlayPosition: { x, y },
        } = graphModel.getPointByClient({
          x: e.x,
          y: e.y,
        })
        const crossPoint = getClosestPointOfPolyline(
          {
            x,
            y,
          },
          polylineEdgeModel.points,
        )
        polylineEdgeModel.dbClickPosition = crossPoint
      }
      graphModel.eventCenter.emit(EventType.EDGE_DBCLICK, {
        data: edgeData,
        e,
        position,
      })
    } else {
      // å•å‡»
      // è¾¹å³å‡»ä¹Ÿä¼šè§¦å‘mouseupäº‹ä»¶ï¼Œåˆ¤æ–­æ˜¯å¦æœ‰å³å‡»ï¼Œå¦‚æžœæœ‰å³å‡»åˆ™å–æ¶ˆç‚¹å‡»äº‹ä»¶è§¦å‘
      // è¾¹æ•°æ®
      graphModel.eventCenter.emit(EventType.ELEMENT_CLICK, {
        data: edgeData,
        e,
        position,
      })
      graphModel.eventCenter.emit(EventType.EDGE_CLICK, {
        data: edgeData,
        e,
        position,
      })
    }
    const { editConfigModel } = graphModel
    graphModel.selectEdgeById(model.id, isMultipleSelect(e, editConfigModel))
    this.toFront()
  }

  /**
   * @overridable æ”¯æŒé‡å†™, æ­¤æ–¹æ³•ä¸ºèŽ·å–è¾¹çš„å½¢çŠ¶ï¼Œå¦‚æžœéœ€è¦è‡ªå®šä¹‰è¾¹çš„å½¢çŠ¶ï¼Œè¯·é‡å†™æ­¤æ–¹æ³•ã€‚
   * @example https://docs.logic-flow.cn/docs/#/zh/guide/basic/edge?id=%e5%9f%ba%e4%ba%8e-react-%e7%bb%84%e4%bb%b6%e8%87%aa%e5%ae%9a%e4%b9%89%e8%be%b9
   */
  getEdge(): h.JSX.Element | null {
    return null
  }

  /**
   * @overridable æ”¯æŒé‡å†™, æ­¤æ–¹æ³•ä¸ºè¾¹åœ¨è¢«é€‰ä¸­æ—¶å°†å…¶ç½®é¡¶ï¼Œå¦‚æžœä¸éœ€è¦æ­¤åŠŸèƒ½ï¼Œå¯ä»¥é‡å†™æ­¤æ–¹æ³•ã€‚
   */
  toFront() {
    const { graphModel, model } = this.props
    graphModel.toFront(model.id)
  }

  /**
   * ä¸å»ºè®®é‡å†™ï¼Œå¦‚æžœè¦è‡ªå®šä¹‰è¾¹çš„å½¢çŠ¶ï¼Œè¯·é‡å†™getEdgeæ–¹æ³•ã€‚
   */
  render() {
    const {
      model: { isSelected, isHitable, isShowAdjustPoint },
    } = this.props
    return (
      <g>
        <g
          className={[
            'lf-edge',
            !isHitable && 'pointer-none',
            isSelected && 'lf-edge-selected',
          ]
            .filter(Boolean)
            .join(' ')}
          onMouseDown={this.handleMouseDown}
          onMouseUp={this.handleMouseUp}
          onContextMenu={this.handleContextMenu}
          onMouseOver={this.setHoverOn}
          onMouseEnter={this.setHoverOn}
          onMouseLeave={this.setHoverOff}
        >
          {this.getShape()}
          {this.getAppend()}
          {this.getText()}
          {this.getArrow()}
        </g>
        {isShowAdjustPoint && isSelected ? this.getAdjustPoints() : ''}
      </g>
    )
  }
}

export default BaseEdge
