import { debounce, isEqual, last, cloneDeep } from 'lodash-es'
import { deepObserve, IDisposer } from 'mobx-utils'
import LogicFlow from '..'
import { GraphModel } from '../model'
import { EventType } from '../constant'
import EventEmitter from '../event/eventEmitter'

import GraphData = LogicFlow.GraphData

export class History {
  undos: GraphData[] = []
  redos: GraphData[] = []
  callbacks = []
  stopWatch: IDisposer | null = null
  curData: GraphData | null = null
  maxSize = 50
  // å‘ç”Ÿæ•°æ®å˜åŒ–åŽï¼Œæœ€å¤šå†ç­‰500msï¼ŒæŠŠè·ç¦»ä¸Šæ¬¡çš„æ•°æ®å˜æ›´å­˜å‚¨èµ·æ¥ã€‚
  // æ‰€ä»¥waitTimeå€¼è¶Šå°ï¼ŒHistoryå¯¹æ•°æ®å˜åŒ–è¶Šæ•æ„Ÿï¼Œå­˜çš„undoså°±è¶Šç»†ã€‚
  waitTime = 100
  eventCenter: EventEmitter

  constructor(eventCenter: EventEmitter) {
    this.eventCenter = eventCenter
  }

  add(data: GraphData) {
    if (isEqual(last(this.undos), data)) return
    this.undos.push(data)
    // å› ä¸ºundoçš„æ—¶å€™ï¼Œä¼šè§¦å‘add.
    // æ‰€ä»¥éœ€è¦åŒºåˆ†è¿™ä¸ªaddæ˜¯undoè§¦å‘çš„ï¼Œè¿˜æ˜¯ç”¨æˆ·æ­£å¸¸æ“ä½œè§¦å‘çš„ã€‚
    // å¦‚æžœæ˜¯ç”¨æˆ·æ­£å¸¸æ“ä½œè§¦å‘çš„ï¼Œéœ€è¦æ¸…ç©ºredos
    if (!isEqual(this.curData, data)) {
      this.redos = []
    }
    this.eventCenter.emit(EventType.HISTORY_CHANGE, {
      data: {
        undos: this.undos,
        redos: this.redos,
        undoAble: this.undoAble(),
        redoAble: this.redoAble(),
      },
    })
    if (this.undos.length > this.maxSize) {
      this.undos.shift()
    }
  }

  undoAble() {
    // undosæ ˆé¡¶å°±æ˜¯å½“å‰å›¾æ¸²æŸ“å‡ºæ¥çš„æ•°æ®ã€‚
    return this.undos.length > 1
  }

  // 1) undoæ–¹æ³•è§¦å‘
  // 2) graphModelé‡æ–°æ¸²æŸ“nodeså’Œedges
  // 3) graphModelå‘ç”Ÿå˜åŒ–ï¼Œè§¦å‘watch
  // 4) watchè§¦å‘add
  undo() {
    if (!this.undoAble()) return
    const preData = this.undos.pop()!
    this.redos.push(preData)
    const curData = this.undos.pop()!
    this.curData = cloneDeep(curData)
    return curData
  }

  redoAble() {
    return this.redos.length > 0
  }

  redo() {
    if (!this.redoAble()) return
    const curData = this.redos.pop()!
    this.curData = cloneDeep(curData)
    return curData
  }

  watch(model: GraphModel) {
    this.stopWatch && this.stopWatch()

    // æŠŠå½“å‰watchçš„modelè½¬æ¢ä¸€ä¸‹æ•°æ®å­˜èµ·æ¥ï¼Œæ— éœ€æ¸…ç©ºredosã€‚
    this.undos.push(model.modelToGraphData())

    this.stopWatch = deepObserve(
      // TODOï¼šé¿å…ç”¨æˆ·è§¦å‘ã€ŒThe same observable object cannot appear twice in the same treeã€ é”™è¯¯
      // ä¾‹å¦‚ï¼šåœ¨è‡ªå®šä¹‰èŠ‚ç‚¹çš„ setAttributes æ–¹æ³•ä¸­ï¼Œå°† nodeModel å±žæ€§èµ‹å€¼ç»™å¦ä¸€ä¸ª observable å±žæ€§
      // eg:
      // setAttributes() {
      //   this.width = 120
      //   this.height = 50
      //
      //   if (this.text) {
      //     this.properties.text = this.text;
      //     this.text.value = '';
      //   }
      // }
      // è§£å†³æ–¹æ¡ˆï¼šä½¿ç”¨ cloneDeep æ–¹æ³•ï¼Œå°† observable å¯¹è±¡å…‹éš†ä¸€ä»½ã€‚éœ€è¦æµ‹è¯•ä¸‹é¢æ“ä½œæ˜¯å¦ä¼šé€ æˆå…¶å®ƒé—®é¢˜
      // https://stackoverflow.com/questions/55328504/a-node-cannot-exists-twice-in-the-state-tree-mobx-state-tree
      // cloneDeep(model),
      model,
      debounce(() => {
        // æ•°æ®å˜æ›´åŽï¼ŒæŠŠæœ€æ–°çš„å½“å‰modelæ•°æ®å­˜èµ·æ¥ï¼Œå¹¶æ¸…ç©º redosã€‚
        // å› ä¸ºè¿™ä¸ªå›žè°ƒå‡½æ•°çš„è§¦å‘ï¼Œä¸€èˆ¬æ˜¯ç”¨æˆ·äº¤äº’è€Œå¼•èµ·çš„ï¼Œæ‰€ä»¥æŒ‰æ­£å¸¸é€»è¾‘éœ€è¦æ¸…ç©º redosã€‚
        const data = model.modelToHistoryData()
        if (data) {
          this.add({ ...data })
        }
      }, this.waitTime),
    )
  }
}

export default History
